#hamMenu {
  min-height: 55px;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  display: block;
  top: 0;
  left: 0;
  font-family: "Orbitron", sans-serif;
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
span.mdl-layout__title,
span.mdl-layout-title {
  font-family: "Orbitron", sans-serif;
}
#hamMenu .mdl-layout__header-row {
  height: 56px;
}
body {
  height: 100%;
  width: 100%;
  background: #252121; /* fallback for old browsers */
}
div.mdl-layout.has-scrolling-header .mdl-layout__content {
  overflow-x: hidden;
}

#landing {
  display: flex;
  flex-flow: column;
  align-content: space-between;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 56px);
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
#landing h1 {
  font-family: "Orbitron", sans-serif;
  width: 80%;
  margin: 0 auto;
  font-size: 8rem;
}
