#landing .newLine {
  display: block;
  word-wrap: break-word;
}
#aboutMe .newLine {
  display: block;
  word-wrap: break-word;
}
#aboutMe .bold {
  font-weight: bold;
  font-size: 30px;
}
#landing .social-links {
  display: flex;
  justify-content: space-between;
  width: 30vw;
  margin-top: 5%;
}
#landing .social-links i {
  font-size: 5em;
  color: rgba(214, 202, 202, 0.808);
}
#landing .social-links i:hover {
  color: rgb(255, 255, 255);
}
#aboutMe {
  background: #cccccc;
  color: black;
  display: flex;
  align-items: center;
  font-family: "Raleway", sans-serif;
  padding: 2.5% 0 0 0;
}
#aboutMe .left {
  margin-left: 5%;
  width: 35%;
}
#aboutMe .right {
  margin-right: 5%;
  width: 50%;
  margin-left: 5%;
  font-size: 20px;
}
#aboutMe .left .title {
  margin-top: 15px;
  font-weight: bold;
  font-family: "Orbitron", sans-serif;
  font-size: 45px;
  text-decoration: underline;
  padding: 1.75% 0;
}
#aboutMe .right .title {
  margin-bottom: 15px;
  font-size: 25px;
}
#aboutMe .left h2 {
  font-family: "Raleway", sans-serif;
  font-style: italic;
  font-size: 22.5px;
  line-height: 3rem;
}
#aboutMe .right .languages {
  display: flex;
  justify-content: space-evenly;
  font-weight: bolder;
  margin: 5% 0;
  padding: 2.5% 1%;
  background-color: #575757;
  filter: opacity(80%);
  color: rgb(255, 255, 255);
  border: none;
  padding: 2.5% 1%;
}
#aboutMe .right h2 {
  font-family: "Orbitron", sans-serif;
  font-style: italic;
  font-size: 25px;
  font-weight: bold;
}
#aboutMe p {
  font-size: 25px;
  line-height: 2.75rem;
}
#resume {
  background: #cccccc;
  color: black;
  font-family: "Raleway", sans-serif;
  padding: 0.25% 0 0.25% 0;
}
#resume .button {
  text-decoration: none;
  margin: 1.25% auto 2.5% auto;
  padding: 1.25% 1.75%;
  width: 20%;
  background-color: #1e5e77;
  transition: all 0.5s;
  border-radius: 10px;
  border: none;
}
#resume .button a {
  text-decoration: none;
  color: rgb(230, 220, 220);
  font-style: unset;
}
#resume .button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  font-style: normal;
  font-size: 1.5em;
}
#resume .button span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
#resume .button:hover span {
  padding-right: 25px;
}
#resume .button:hover span:after {
  opacity: 1;
  right: 0;
}
#resume .wrapper {
  background-color: #17171b;
  margin: 5%;
  color: whitesmoke;
}
#resume #education,
#resume #experience,
#resume #awards {
  margin: 0 5%;
  padding: 1.25% 0 2.5% 0;
  width: 90%;
  border-bottom: 2.5px solid #b42929;
  text-align: left;
}
#resume #awards {
  border-bottom: none;
}
#resume .section-title {
  font-size: 3rem;
  font-family: "Orbitron", sans-serif;
}
#resume .title {
  font-size: 2.5rem;
  margin: 1.25% 0 0 0;
  line-height: 35px;
  color: #4e517e;
}
#resume .subtitle {
  font-size: 1.25rem;
  font-style: italic;
  line-height: 25px;
  margin: 0;
  color: #c1edff;
}
#resume .bullets {
  padding-left: 2.5%;
  font-size: 1.25rem;
  color: #e6e6e6;
}
.content {
  color: white;
  height: auto;
  width: 100%;
  text-align: center;
  background: #cccccc;
}
.content h1 {
  margin: 0;
}
.link::after {
  content: "";
  display: block;
  position: relative;
  top: -17.5px;
  width: 0;
  height: 2px;
  background-color: white;
  transition: width 0.3s;
}

.link:hover::after {
  width: 100%;
  transition: width 0.3s;
}

div.mdl-layout__drawer-button {
  display: none;
}
@media screen and (max-width: 1024px) {
  #hamMenu {
    display: block;
  }
  div.mdl-layout__drawer-button {
    color: white;
  }
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 15px;
  }
  #landing h1 {
    font-size: 10vw;
  }
  #landing .social-links {
    width: 30vw;
  }
  #landing .social-links i {
    font-size: 4.5em;
  }
}

@media screen and (max-width: 780px) {
  #hamMenu {
    position: sticky;
  }
  .mdl-layout__header-row .mdl-navigation {
    display: none;
  }
  .mdl-layout__header-row > * {
    width: 100%;
    text-align: right;
  }
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 1.125rem;
  }
  div.mdl-layout__drawer-button {
    display: block;
  }
  #landing .social-links {
    width: 30vw;
  }
  #landing .social-links i {
    font-size: 3em;
  }
}

@media screen and (max-width: 480px) {
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 0.875rem;
  }
  div.mdl-layout__drawer {
    width: 240px;
  }
  #landing h1 {
    font-size: 50px;
  }
  #landing .social-links {
    width: 50vw;
  }
  #landing .social-links i {
    font-size: 50px;
  }
}
@media screen and (max-width: 300px) {
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 0.625rem;
  }
  div.mdl-layout__drawer {
    width: 160px;
  }
  #landing h1 {
    font-size: 35px;
  }
  #landing .social-links {
    width: 50vw;
  }
  #landing .social-links i {
    font-size: 30px;
  }
}
